html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.tagline {
  font-family: "Inter", sans-serif;
  font-weight: 900;
  color: #000;
  font-size: 3rem;
  text-align: center;
}

.descripcion-pagina {
  text-align: center;
}

/** Media Queries **/
.formulario {
  margin-top: 5rem;
}
.formulario .campo {
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
}
.formulario .campo label {
  flex: 0 0 10rem;
}
.formulario .campo input {
  flex: 1;
  padding: 1rem;
  border: none;
  background-color: #d8dadf;
}

.acciones {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  text-align: center;
  margin-bottom: 2rem;
  color: #000;
}
.acciones a:last-of-type {
  margin-bottom: 0;
}

.header {
  background: #3ab2f3;
  padding-top: 2rem;
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 0;
  margin: 0;
}
.barra .menu-hamburguesa {
  width: 5rem;
  filter: invert(100%);
}
.barra .menu-hamburguesa:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
  .barra .menu-hamburguesa {
    display: none;
  }
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}

.izquierda {
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .izquierda {
    align-items: flex-end;
    flex-direction: row;
  }
}
.izquierda img {
  max-width: 22rem;
}
.izquierda a {
  width: 15vh;
}
.izquierda h1 {
  text-transform: uppercase;
  color: #FFFFFF;
}

.navegacion {
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
  margin: 3rem 0 0 0;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
    margin: 0;
  }
}
.navegacion a {
  color: #000;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
  .navegacion a:hover {
    color: #FFFFFF;
  }
}

/**Menu de hamburguesa **/
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.ver {
  display: none;
  visibility: visible;
  opacity: 1;
  height: auto;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
}
@media (min-width: 768px) {
  .ver {
    display: flex;
  }
}

.mostrar {
  display: flex;
  flex-direction: column;
}

.login {
  margin-top: 5rem;
}
.login .uptask {
  background: linear-gradient(to right, #e28f2d 0%, #f5d7b3 100%);
  color: transparent;
  background-clip: text;
  font-size: 7rem;
  text-align: center;
}
@media (min-width: 768px) {
  .login .uptask {
    font-size: 10rem;
  }
}
.login input[type=submit] {
  background-color: #e28f2d;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .login input[type=submit] {
    width: auto;
  }
}
.login input[type=submit]:hover {
  background-color: #c1751b;
  cursor: pointer;
}
.login .acciones a {
  color: #e28f2d;
}

